.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.todo-section, .timer-section {
  margin-bottom: 20px;
}

.todo-input {
  display: flex;
  margin-bottom: 20px;
}

.todo-input input {
  flex-grow: 1;
  padding: 10px;
  font-size: 16px;
}

.todo-input button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.todo-list {
  list-style-type: none;
  padding: 0;
}

.todo-list li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.todo-list li > * {
  margin-right: 10px;
}

.timer {
  text-align: center;
}

.timer h2 {
  font-size: 48px;
  margin-bottom: 20px;
  touch-action: none;
  -webkit-user-select: none;
  user-select: none;
}

.timer-controls button {
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 18px;
  background-color: #008CBA;
  color: white;
  border: none;
  cursor: pointer;
}

/* Desktop layout */
@media (min-width: 768px) {
  .app-container {
    display: flex;
    justify-content: space-between;
  }

  .todo-screen {
    width: 48%;
  }

  .timer-screen {
    width: 48%;
    opacity: 1 !important;
    pointer-events: auto !important;
  }

  .start-button-container {
    position: static;
    margin-top: 20px;
  }

  .screen.hidden {
    display: block;
    opacity: 1;
    pointer-events: auto;
  }
}

/* Mobile layout */
@media (max-width: 767px) {
  .app-container {
    display: block;
  }

  .todo-screen, .timer-screen {
    width: 100%;
  }

  .screen {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: auto;
  }

  .screen.hidden {
    opacity: 0;
    pointer-events: none;
  }
}

.start-button-container {
  position: fixed;
  bottom: 20px;
  left: 20px;
  z-index: 1000;
}

.start-button {
  padding: 15px 30px;
  font-size: 24px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.start-button:hover {
  background-color: #45a049;
}

.start-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.screen {
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
}

.screen.hidden {
  opacity: 0;
  pointer-events: none;
}
